/* custom checkbox styling for contact form 7 checkbox */
span.wpcf7-list-item {
  display: inline-block;
  margin: 0;
}

.wpcf7 .wpcf7-form label {
  display: flex;
  column-gap: 10px;
}

.wpcf7 .wpcf7-list-item {
  display: block;
  margin-bottom: 10px;
}

.wpcf7-checkbox {
  display: flex;
}

.acceptance.wpcf7-checkbox {
  margin-bottom: 0.5rem;
}

.contact-form-block .acceptance.wpcf7-checkbox {
  margin: 0.5rem 0;
}

.contact-us-block .acceptance .wpcf7-list-item-label {
  font-size: 0.8rem;
}

.wpcf7-checkbox label {
  position: relative;
  cursor: pointer;
}

.wpcf7-checkbox input[type=checkbox] {
  position: absolute;
  visibility: hidden;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
}

.wpcf7-checkbox input[type=checkbox] + span:before {
  display: block;
  position: absolute;
  content: "";
  border-radius: 0;
  height: 20px;
  width: 20px;
  top: 0px;
  left: 0px;
  background-color: #fff;
  border: 1px solid #999;
}

.wpcf7-checkbox input[type=checkbox] + span:after {
  display: block;
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  top: -2px;
  left: 0px;
  visibility: hidden;
  font-size: 16px;
  text-align: center;
}

.wpcf7-checkbox input[type=checkbox]:checked + span:after {
  visibility: visible;
  color: var(--primary);
}

.wpcf7-list-item-label {
  margin-left: 30px;
}

