  margin-bottom: 0;
}
@media (max-width: 1199.98px) {
  .blog-footer-header {
    margin-bottom: 2rem;
  }
  .blog-footer-header h3, .blog-footer-header .h3 {
    margin-bottom: 1rem;
  }
  .blog-footer-header .reviews-slider__logo-link {
    margin-bottom: 0;
  }
}

/**************************************/
/* !Blog Slider Feed                  */
/**************************************/
.blog-carousel {
  position: relative;
}
.blog-carousel .blog-content .btn, .blog-carousel .blog-content .wpcf7 input[type=submit], .wpcf7 .blog-carousel .blog-content input[type=submit], .blog-carousel .blog-content .woocommerce-notices-wrapper .woocommerce-message a.button, .woocommerce-notices-wrapper .woocommerce-message .blog-carousel .blog-content a.button, .blog-carousel .blog-content .woocommerce-info a.button, .woocommerce-info .blog-carousel .blog-content a.button {
  margin-top: auto;
}

.blog-carousel .tns-controls {
  position: absolute;
  bottom: 50px;
  left: 39%;
  pointer-events: none;
  z-index: 1;
  display: flex;
  gap: 40px;
}
@media (max-width: 1199.98px) {
  .blog-carousel .tns-controls {
    top: -67px;
    bottom: unset;
    right: 0;
    left: unset;
  }
}

.blog-carousel .tns-controls button {
  border: none;
  text-align: center;
  pointer-events: visible;
  color: var(--primary);
  font-size: 1.5rem;
  background-color: transparent;
  display: flex;
}
.blog-carousel .tns-controls button .icon-arrow-right-big:after,
.blog-carousel .tns-controls button .icon-arrow-left-big:after {
  content: "";
  width: 30px;
  border-top: 2px solid var(--primary);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.blog-carousel .tns-controls button .icon-arrow-right-big:after {
  left: 9px;
}
.blog-carousel .tns-controls button .icon-arrow-left-big:after {
  right: 9px;
}

.blog-carousel .tns-controls button:hover {
  color: var(--accent-green);
}
.blog-carousel .tns-controls button:hover .icon-arrow-left-big {
  transform: translateX(10px);
}
.blog-carousel .tns-controls button:hover .icon-arrow-right-big {
  transform: translateX(-10px);
}
.blog-carousel .tns-controls button:hover .icon-arrow-right-big:after {
  width: 40px;
  left: 3px;
  border-color: var(--accent-green);
}
.blog-carousel .tns-controls button:hover .icon-arrow-left-big:after {
  width: 40px;
  right: 3px;
  border-color: var(--accent-green);
}

/**************************************/
/* !Lightbox Gallery                  */
/**************************************/
.lg-on body {
  overflow: hidden;
}

.lg-next,
.lg-prev {
  background-color: var(--secondary);
  color: var(--primary);
  cursor: pointer;
  display: block;
  font-size: 22px;
  position: absolute;
  top: 50%;
  z-index: 1084;
  outline: none;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
}

.lg-next.disabled,
.lg-prev.disabled {
  opacity: 0 !important;
  cursor: default;
}

.lg-next:hover:not(.disabled),
.lg-prev:hover:not(.disabled) {
  color: #fff;
}

.lg-single-item .lg-next,
.lg-single-item .lg-prev {
  display: none;
}

.lg-next {
  right: 1rem;
}

.lg-next:before {
  content: "\e91c";
  font-family: "icomoon" !important;
}

.lg-prev {
  left: 1rem;
}

.lg-prev:after {
  content: "\e91a";
  font-family: "icomoon" !important;
}

.lg-sub-html {
  position: absolute;
  bottom: 10px;
  right: 0;
  left: 0;
  color: var(--white);
  background-color: rgba(0, 0, 0, 0.5019607843);
  width: fit-content;
  margin: auto;
  padding: 8px 50px 10px;
  text-transform: capitalize;
}
.lg-sub-html p {
  margin: 0;
}

.lg-outer::after {
  content: "\e917";
  font-family: "icomoon" !important;
  position: absolute;
  top: 3rem;
  right: 1rem;
  cursor: pointer;
  z-index: 9999;
  background-color: var(--secondary);
  color: var(--primary);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lg-backdrop {
  background-color: var(--primary-modal) !important;
}

.lg-outer .lg-thumb-outer {
  background-color: transparent;
}

@media (max-width: 992px) {
  .lg-next,
  .lg-prev {
    top: 85%;
    font-size: 18px;
    width: 30px;
    height: 30px;
  }
  .lg-next {
    right: 40%;
  }
  .lg-prev {
    left: 40%;
  }
}
#lg-toolbar-1 {
  display: none;
}

